<template>
	<div class="index-board">
		<div class="approve">
			<div class="content-describe">
				您需要授权一定量的LT用于支付<br/>
				如果剩余额度小于支付额度，将支付失败<br>
				剩余授权额度：{{approveGetVal}} LT<br>
				设置授权额度：<Input v-model="approveSetVal" size="large" style="width: 150px" />
			</div>
			<div class="btn-con">
				<Button type="primary" size="large" @click="goApprove">授权</Button>
			</div>
		</div>
		<div class="payment">
			<div class="content-describe">
				您的自治组织准备好了！<br/>
				现在需要您支付1000LT，<br/>我们将审核您已提交的信息，审核通过后，<br/>您将在LittleBeeX上治理您的公司！
			</div>
			<div class="btn-con">
				<Button type="primary" size="large" @click="goPay">支付LT</Button>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import {tokenAddress} from '@/util/constants/casinoContract'
	export default {
		data(){
			return{
				approveGetVal: 360,
				approveSetVal: 0
			}
		},
		computed: {
			...mapState({
				Address: state => state.web3.coinbase
			})
		},	
		methods:{
			goPay(){
				//this.$router.push({path:'../home/Overview'}),
			    const paycode = 1000  * (10 ** 18);
				this.$store.state.tokenInstance().methods.transferFrom(this.Address,"0x79793BE2287e05490058acb17513B2BfAcdfe070","6000000000000000000000").send({
					from: this.Address
				}).then(result => {
					console.log(result)
					console.log('asdasd');
				}).on('confirmation',function(number, receipt){
					let data = {
						"address": this.Address,
						"only": this.$route.query.only
					};
					this.$axios({
						method: 'post',
						url: '/index.php/cn/home/node_su/order',
						data: Qs.stringify(data)
					}).then((response) => {
						this.$Notice.warning({
							title: '操作成功！等待人员审核'
						});
					})
				})
			},
			goApprove(){
				const paycode = this.approveSetVal  * (10 ** 18);
				this.$store.state.tokenInstance().methods.mint(tokenAddress, paycode).send({
					from: this.Address
				}).then(result => {
					console.log(result)
					console.log('asdasd');
				})
			}
		}
	}
</script>
<style scoped lang="stylus">
	.index-board
		padding: 80px 100 !important;
		height: 570px !important;
		display: flex;
		display: -webkit-flex;
		align-items: center;
		justify-content: space-between;
		box-shadow: none !important;
		background: transparent !important;
		.approve,.payment
			display: flex;
			display: -webkit-flex;
			flex-direction: column;
			justify-content: space-between;
			min-height: 400px;
			box-shadow: 0 2px 7px rgba(0,0,0,0.15);
			padding: 50px 70px;
			background-color: #fff;
			.content-describe
				font-size: 16px;
				width: 100%;
				line-height: 36px;
			.btn-con
				text-align: center;
				button
					padding: 8px 40px;
					font-size: 16px;
</style>


